:root{
  --bg: #0f1115;
  --panel: #151923;
  --panel-2: #1b2130;
  --text: #eef3ff;
  --muted: #aab3c5;
  --accent: #6aa9ff;
  --accent-2: #9bdbff;
  --ok: #45c486;
  --warn: #ffc14d;
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  background: radial-gradient(1200px 800px at 70% -10%, #1d2435 0%, #0f1115 50%, #0b0d11 100%);
  color: var(--text);
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, "Helvetica Neue", Arial, "Noto Sans", "Apple Color Emoji", "Segoe UI Emoji";
}

.page{
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  gap: 18px;
}

.header{
  padding: 28px 18px 0;
  text-align: center;
}
.header h1{
  margin: 0 0 6px;
  font-size: clamp(22px, 3vw, 32px);
  letter-spacing: 0.2px;
}
.sub{
  margin: 0 auto;
  max-width: 720px;
  color: var(--muted);
  font-size: 14px;
}

.card{
  width: min(960px, 92vw);
  margin: 0 auto;
  background: linear-gradient(180deg, var(--panel) 0%, var(--panel-2) 100%);
  border: 1px solid rgba(255,255,255,0.06);
  border-radius: 18px;
  padding: clamp(16px, 2.5vw, 28px);
  box-shadow: 0 12px 40px rgba(0,0,0,0.35);
}

.formRow{
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 14px;
  align-items: center;
}
.label{
  font-weight: 600;
  color: var(--muted);
}
.input{
  width: 100%;
  padding: 12px 14px;
  border-radius: 12px;
  background: #0e1320;
  color: var(--text);
  border: 1px solid rgba(255,255,255,0.1);
  outline: none;
  transition: border .2s, box-shadow .2s;
}
.input:focus{
  border-color: var(--accent);
  box-shadow: 0 0 0 4px rgba(106,169,255,0.15);
}

.ageRow{
  margin-top: 16px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.pill{
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  background: rgba(106,169,255,0.1);
  border: 1px solid rgba(106,169,255,0.25);
  border-radius: 999px;
}
.pill.alt{
  background: rgba(69,196,134,0.1);
  border-color: rgba(69,196,134,0.25);
}
.pillLabel{
  color: var(--muted);
  font-size: 12px;
  letter-spacing: .2px;
}
.pillValue{
  font-weight: 700;
  font-variant-numeric: tabular-nums;
}

.results{
  margin-top: 22px;
}
.results h2{
  margin: 0 0 10px;
  font-size: clamp(18px, 2.4vw, 22px);
}
.empty{
  color: var(--muted);
  background: rgba(255,255,255,0.04);
  border: 1px dashed rgba(255,255,255,0.15);
  padding: 16px;
  border-radius: 12px;
}

.vaxList{
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 10px;
}
.vaxItem{
  background: rgba(255,255,255,0.04);
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 14px;
  padding: 12px;
  display: grid;
  gap: 6px;
}
.vaxName{
  font-weight: 700;
}
.vaxNote{
  color: var(--muted);
  font-size: 13px;
  line-height: 1.4;
}

/* Legend Card */
.legendCard{
  margin-top:16px;
  border:1px solid rgba(255,255,255,0.08);
  border-radius:14px;
  background: rgba(255,255,255,0.03);
  overflow:hidden;
}
.legendHeader{
  width:100%;
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:12px;
  padding:12px 14px;
  background: linear-gradient(180deg, rgba(255,255,255,0.04), rgba(255,255,255,0.02));
  color: var(--text);
  border:0;
  cursor:pointer;
  font-weight:700;
  letter-spacing:.2px;
}
.legendHeader .chev{
  transition: transform .2s ease;
  opacity:.8;
}
.legendCard.open .legendHeader .chev{
  transform: rotate(180deg);
}

.legendBody{
  display:grid;
  gap:14px;
  padding: 12px 14px 14px;
  max-height:0;
  overflow:hidden;
  transition: max-height .25s ease;
}
.legendCard.open .legendBody{
  max-height: 600px; /* enough for content; keeps a smooth slide */
}

/* Abbreviations grid */
.abbrGrid{
  display:grid;
  grid-template-columns: repeat(2, minmax(0,1fr));
  gap:10px;
}
@media (max-width: 640px){
  .abbrGrid{ grid-template-columns: 1fr; }
}
.abbrItem{
  display:grid;
  grid-template-columns: auto 1fr;
  gap:8px 10px;
  align-items:start;
  padding:10px 12px;
  background: rgba(255,255,255,0.04);
  border:1px solid rgba(255,255,255,0.08);
  border-radius:12px;
}
.abbrKey{
  display:inline-flex;
  align-items:center;
  justify-content:center;
  height:28px;
  padding:0 10px;
  border-radius:999px;
  font-weight:800;
  font-size:12px;
  letter-spacing:.3px;
  background: rgba(106,169,255,0.14);
  border:1px solid rgba(106,169,255,0.35);
  color: var(--text);
  white-space:nowrap;
}
.abbrDef{
  color: var(--muted);
  font-size:13px;
  line-height:1.35;
}

/* Callouts */
.callouts{ display:grid; gap:10px; }
.callout{
  padding:12px;
  border-radius:12px;
  border:1px solid rgba(255,255,255,0.12);
  background: rgba(255,255,255,0.05);
  font-size:13px;
  line-height:1.4;
}
.callout strong{ font-weight:800; }
.callout.warn{
  background: rgba(255,193,77,0.12);
  border-color: rgba(255,193,77,0.35);
}
.callout.info{
  background: rgba(106,169,255,0.12);
  border-color: rgba(106,169,255,0.35);
}


.disclaimer{
  margin-top: 14px;
  color: var(--muted);
  font-size: 12px;
}

.footer{
  padding: 20px;
  text-align: center;
  color: var(--muted);
  font-size: 12px;
}

/* Responsive tweaks */
@media (max-width: 640px){
  .formRow{
    grid-template-columns: 1fr;
  }
  .label{ margin-bottom: -6px; }
}
